.menu_parent_wrapper {
    position: fixed;
    height: 10vh;
    left: 0%;
    right: 0%;
    padding: 0 3%;
    z-index: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: hidden;
}

.menu_wrapper {
    width: 100%;
}

.menu_wrapper ul {
    height: 100%;
    margin: 0%;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5%;
    color: #000;
    font: 500 1rem sans-serif;
    text-decoration: none;
}

.menu_wrapper ul li:nth-of-type(1) {
    margin-left: auto;
}

.menu_wrapper ul li:nth-of-type(5) {
    margin-left: auto;
    margin-right: 3%;
    display: flex;
    gap: 20%;
}

.checked {
    display: flex;
    border-bottom: 1px solid red;
}

.menu_parent_wrapper.active {
    background-color: #fff;
}

.icon {
    font-size: 1.6rem;
    color: rgb(172, 172, 172);
    background-color: hsl(0, 0%, 0%, 0.8);
    border-radius: 50%;
    width: 3vw;
    height: 3vw;
    padding: 0.4rem;
}

.menu_wrapper ul>* {
    cursor: pointer;
    user-select: none;
}

a {
    text-decoration: unset;
    color: unset;
}

.logo_wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

@media screen and (max-width: 600px) {
    .parent_wrapper {
        position: fixed;
        height: 100vh;
        width: 100vw;
        display: flex;
        left: -100%;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        background-color: #fff;
    }
    .parent_wrapper>* {
        width: 100%;
    }
    .menu_wrapper {
        height: 100%;
        width: 100%;
    }
    .menu_wrapper ul {
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        color: #353553;
        gap: 5%;
        padding: 0 10%;
        font: 500 0.8rem sans-serif;
    }
    .icon {
        font-size: 2rem;
        background: none;
    }
    .Profile::after {
        content: "Account";
        position: absolute;
        right: 2%;
    }
    .menu_wrapper ul>* {
        cursor: pointer;
        user-select: none;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        color: rgb(109, 109, 109);
    }
    .menu_wrapper ul>*:first-child {
        margin: 0 0%;
    }
    .logo_wrapper {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
}